.facility-container{
    margin-top: 50px;
    margin-bottom: 50px;
}

.facility-item{
    margin: 5px 0;
}

.facility-icon{
    border-radius: 50%;
    background-color: #ffffff;
    padding: 20px 20px;
    margin-bottom: -30px;
    color: #C8AC86;
}

.facility-text{
    height: 130px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 500;
    background-color: #F8F9FA;
}
.facility-text h3{
    font-size: 24px;
}
.facility-item:hover .facility-text{
    background-color: #C8AC86;
    color: #ffffff;
}

